.thumb-login{
	margin-bottom: 30px;
	display:flex;
	flex-direction:row;
	justify-content:space-between;
    border-radius: 5px;
    align-items: center;
    background: #fff;
    box-shadow: 0px 1px 3px 0px rgba(43, 54, 79, 0.08);
    padding: 25px;
}
.thumb-login .thumb__avatar{
	background: #e8ecf5;
    margin: 0 15px 0 0;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    width: 40px;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    height: 40px;
    border-radius: 50%;
}
.thumb-login .thumb__image {
	display:flex;
	flex-direction:row;
	align-items: center;
	justify-content:flex-start;
}
.thumb-login .thumb__image .box-name{
	font-size:12px;
	color:#434f6a;
}
.thumb-login .thumb__image .name{
	color:#434f6a;
	font-size:18px;
	font-weight:600;
	display:block;
}
.thumb-login .thumb__image,
.thumb-login .login-ads,
.thumb-login .login-history-time {
	width:33%;
	flex:0 0 33%;
	color:#151924;
	font-weight:600;
	font-size:14px;
}
.thumb-login .login-ads,
.thumb-login .login-history-time{
	text-align:center;
	border-left:1px solid #e6ebf8;
}
.thumb-login .login-ads .title,
.thumb-login .login-history-time .title{
	display:block;
	color:#888a8e;
	font-weight:400;
	margin-bottom:2px;
}
.thumb-login .thumb__avatar svg {
    width: 14px;
    height: 14px;
}
.sidebar-user_cabinet{
	padding:25px 25px 10px;
	.head-user .img-user{
		width: 55px;
		height: 55px;
		svg{
			width: 20px;
			height: 20px;
		}
	}
	.nav-user_link{
		margin: 20px 0 0;
		ul{
			padding:0;
			margin:0 -25px;
			list-style-type: none;
			li{
				position: relative;
				transition: all .2s ease-in-out;
				a{
					color: #434f6a;
					line-height: 30px;
					display: block;
					padding:10px 10px 10px 25px;
				}
				&:hover{
					background: #f1f4fa;
				}
				&.active{
					background: #f1f4fa;
					&:after{
						left: 0;
						top:0;
						position:absolute;
						content: "";
						height: 100%;
						width: 5px;
						background: #7d8aa5;
					}
				}
			}
		}
	}
}
.nav-user-search{
	background: #fff;
	border-radius: 5px 5px 0 0;
	.sort-content{
		padding:25px;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		border-bottom: 1px solid #e6ebf8;
		.filter__row{
			margin-bottom: 0;
		}
		.filter__form-search{
			display: inline-block;
			margin-right: 25px;
			vertical-align: top;
			position:static;
			.search-icon {
			    display: block;
			    width: 18px;
			    height: 18px;
			    fill: #2b364f;
			    position: absolute;
			    top: 12px;
			    right: 18px;
			}	
		}
		.sort-item{
			display: inline-block;
			vertical-align: top;
		}
	}
}
.user-ads_content{
	background: #fff;
    border-radius: 5px;
    box-shadow: 0px 1px 3px 0px rgba(43, 54, 79, 0.08);
	margin-bottom:70px;
	.pagination{
		margin-bottom: 30px;
	}
}
.ads-items{
	.thumb.adt{
		height: 132px;
		box-shadow: none;
		margin-bottom: 0;
		border-radius:0;
		padding:25px; 
		position: relative;
		&:after{
			content: "";
			left: 25px;
			height: 1px;
			background: #e6ebf8;
			bottom:0;
			right: 25px;
			position: absolute;
		}
	}
	.thumb__head{
		margin-bottom: 0;
		justify-content: flex-start;
	}
	.thumb__footer{
		display: flex;
		flex-direction: row;
	}
	.thumb__info{
		overflow: visible;
		padding-right:30px;
		width: 30%;
		.thumb__title{
			text-overflow: ellipsis;
			overflow: hidden;
			width: 100%;
			white-space: nowrap;
		}
	}
	.thumb__price{
		flex: 1;
		display: flex;
		justify-content: flex-end;
	}
	.thumb_publication{
		border-left:1px solid #e6ebf8;
		padding:5px 25px;
		span{
			display: block;
			margin-bottom: 2px;
		}
		.time{
			display: block;
			color: #151924;
			font-size: 14px;
			font-weight: 600;
		}
	}
	.thumb_view{
		border-left:1px solid #e6ebf8;
		padding:5px 25px;
		span{
			display: block;
			margin-bottom: 2px;
		}
		i{
			font-style: normal;
			display: block;
			color: #151924;
			font-size: 14px;
			font-weight: 600;
		}
	}
	.thumb__buttons {
	    font-weight: 600;
	    width: 61%;
	    -webkit-justify-content: flex-start;
	    -ms-flex-pack: start;
	    justify-content: flex-start;
	    a{
	    	font-weight: 600;
    		color: #888a8e;
    		align-items: flex-end;
    		font-size: 12px;
	    }
	}
}
.thumb__external-link{
	display: flex;
	width: 150px;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
	height: 40px;
	font-weight: 600;
	cursor: pointer;
	svg{
		width: 16px;
		transition: all .2s ease-in-out;
		height: 16px;
		fill:#bfc9e2;
		margin-right: 10px;
	}
	&:hover{
		svg{
			fill:#8397c5;
		}
	}
}
.thumb__pain{
	display: flex;
	width: 150px;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
	height: 40px;
	font-weight: 600;
	cursor: pointer;
	svg{
		width: 16px;
		transition: all .2s ease-in-out;
		height: 16px;
		fill:#bfc9e2;
		margin-right: 10px;
	}
	&:hover{
		svg{
			fill:#8397c5;
		}
	}
}
.thumb__delete{
	display: flex;
	width: 150px;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
	height: 40px;
	font-weight: 600;
	cursor: pointer;
	svg{
		width: 16px;
		height: 16px;
		fill:#bfc9e2;
		transition: all .2s ease-in-out;
		margin-right: 10px;
	}
	&:hover{
		svg{
			fill:#8397c5;
		}
	}
}
.nav-advertise{
	justify-content: flex-end;
	width:41%;
	display:flex;
	.advertise-btn{
		padding:0 32px;
		font-weight: 600;
		width: 150px;
	}
}
.btn-massage{
	width:40px;
	height:40px;
	display:flex;
	border-radius:50%;
	border:1px solid #cbd1e1;
	transition: box-shadow .2s ease-in-out, border-color .2s ease-in-out;
	position: relative;
	svg{
		width: 15px;
		fill:#717a8f;
		height: 12px;
		position: absolute;
		left: 0;
		right: 0;
		top:0;
		margin:auto;
		bottom:0;
	}
	.number{
		border-radius:50%;
		font-style: normal;
		color: #fff;
		position: absolute;
		right: -3px;
		bottom:-3px;
		font-size: 10px;
		width: 16px;
		height: 16px;
		line-height: 15px;
		text-align: center;
		background: #e6a033;
	}
	&:hover{
		border-color: transparent;
    	box-shadow: 0px 7px 24px 0px rgba(49, 62, 90, 0.19);
	}
}
.add-advertisement{
	width:255px;
	border-radius:25px;
	height:45px;
	background: #f1f4fa;
	padding:10px 0;
	margin:0 auto 30px;
	font-size: 15px;
	text-align:center;
	color:#434f6a;
	transition: box-shadow .3s ease-in-out;
	font-weight:600;
	cursor: pointer;
	display:table;
	line-height:25px;
	svg{
		width: 15px;
		height: 15px;
		fill:#bfc9e2;
		margin-right: 8px;
	}
	&:hover{
		box-shadow:0 6px 15px 0 rgba(67, 79, 106, 0.1);
	}
}



