// user-mail

.user-mail_content{
	background: #fff;
	border-radius:5px;
	margin-bottom:70px;
	box-shadow: 0px 1px 3px 0px rgba(43, 54, 79, 0.08);
	.pagination{
		margin-bottom: 30px;
	}
}
.wrap-mail_list{
	.head{
		color: #a6aebf;
		font-size: 14px;
		display: flex;
		flex-direction: row;
	    flex-wrap: nowrap;
		align-items: center;
		padding:13px 25px;
		border-bottom: 1px solid #e6ebf8;
		.th-name{
			padding-left:5px;
			width: 33.5%;
		}
		.th-last-mail{
			padding-left:35px;
			width: 39%;
		}
	}
	.row-body{
		padding-left: 0;
		list-style-type: none;
	}
	.row{
		display: flex;
		flex-direction: row;
		position: relative;
	    flex-wrap: nowrap;
	    cursor: pointer;
		align-items: flex-start;
		background: #fff;
		transition: background-color 0.3s ease;
		padding:22px 25px 22px;
		&:after{
			content: "";
			left: 25px;
			z-index: 2;
			right: 25px;
			background: #e6ebf8;
			height: 1px;
			position: absolute;
			bottom:0;
			transition: all .3s ease-in-out;
		}
		&:before{
			content: "";
			left: 25px;
			right: 25px;
			background: #e6ebf8;
			height: 1px;
			position: absolute;
			top:-1px;
			transition: all .3s ease-in-out;
		}
		&:hover{
			background: #f9f6f0;
			&:after,&:before{
				left: 0;
				right: 0;
				background: #f3eadd;
			}
		}
		> a{
			display: block;
			height: 100%;
		}
		.td{
			display: flex;
			&.col-name{
				width: 33.5%;
				.thumb__title{
					text-overflow: ellipsis;
				    overflow: hidden;
				    width: 100%;
				    white-space: nowrap;
				}
				.thumb__head{
					margin-bottom: 0;
					width: 100%;
				}
			}
			.img-user{
				display: flex;
				align-items: center;
				justify-content: center;
				background: #e8ecf5;
				margin-right: 15px;
				height: 30px;
				border-radius: 50%;
				flex: 0 0 30px;
				svg{
					width: 10px;
					height: 12px;
				}
			}
			&.col-short_mail{
				padding-left:4.3%;
				width: 45%;
				.title{
					color: #434f6a;
					font-size: 14px;
					display: block;
					letter-spacing: 0px;
					font-weight: 600;
					margin-bottom: 5px;
				}
				.info{
					font-size: 13px;
					color: #888a8e;
					letter-spacing: -0.01em;
				}
				.time{
					font-size: 13px;
					color: #151924;
					display: flex;
    				align-items: flex-end;
				}
			}
			&.col-btn_mail{
				align-items: center;
				justify-content: flex-end;
				width: 22%;
				align-self: stretch;
			}
		}
	}
}
