.model-window{
	background: rgba(#121620,0.8);
	position: fixed;
	left:0;
	width:100%;
	top:0;
	height:100%;
	display:none;
	.popup{
		position: absolute;
		left: 0;
		right: 0;
		display: flex;
		flex-direction: column;
		top:0;
		bottom:0;
		width: 570px;
		color: #434f6a;
		margin: auto;
		background: #fff;
		justify-content: space-between;
		align-items: center;
		border-radius:5px;
		.close-btn{
			transition: opacity .3s ease-in-out;
			top:25px;
			margin: auto;
			right: 25px;
			cursor: pointer;
			background: url(../images/close-button.svg) no-repeat;
			width: 10px;
			height: 10px;
			position: absolute;
		}
	}
	.popup-user_message{
		height: 445px;
		.title-popup{
			padding: 30px 0;
			width: 100%;
			line-height:20px;
			font-weight: 400;
			font-size: 24px;
			text-align: center;
			display: block;
			border-bottom:1px solid #e6ebf8;
		}
		.popup-box{
			padding: 35px 50px 50px;
			.thumb__image{
				display: flex;
				margin-bottom: 22px;
				flex-direction: row;
				width: 100%;
			}
			.thumb__avatar{
				background: #e8ecf5;
			    margin: 0 12px 0 0;
			    display: -webkit-flex;
			    display: -ms-flexbox;
			    display: flex;
			    -webkit-align-items: center;
			    -ms-flex-align: center;
			    align-items: center;
			    width: 40px;
			    -webkit-justify-content: center;
			    -ms-flex-pack: center;
			    justify-content: center;
			    height: 40px;
			    border-radius: 50%;
			    svg{
			    	width: 15px;
    				height: 15px;
			    }
			}
			.box-name{
			    .name{
			    	font-size: 18px;
			    	font-weight: 600;
			    	display: block;
			    	color: #434f6a;
			    }
			    .status{
			    	font-size: 12px;
			    	display: block;
			    	color: #434f6a;
			    }
			}
			.text{
				margin-bottom: 12px;
				font-size: 14px;
				line-height:18px;
			}
			textarea{
				width: 100%;
			    font-size: 14px;
			    height: 96px;
			    padding: 10px 18px;
			    resize: none;
			    border-radius: 25px;
			    margin-bottom: 30px;
			    transition: all .3s ease-in-out;
			    color: #434f6a;
			    border: 1px solid #e6ebf8;
			}
			.log__enter{
				display: table;
				margin: 0 auto;
			}
		}
	}
	.popup-hint-info{
		height: 345px;
		padding: 50px;
		color: #151924;
		.i-icon{
			width: 65px;
			height: 65px;
			display: table;
		}
		.text{
			color: #151924;
			line-height:1.6;
			text-align: center;
		}
		.close-btn{
    		background: #2b364f;
    		&:hover{
    			box-shadow: 0px 8px 27px 0px rgba(49, 62, 90, 0.5);
    		}
		}
	}
}
.hint-info_box{
	width:1170px;
	margin:0 auto;
	border-radius: 6px;
	background: #e3eaf9;
	position: relative;
	padding:0 33px;
	display:flex;
	opacity:0;
	visibility:hidden;
	max-height:0;
	flex-direction:row;
	transition: all .5s ease-in-out;
	font-size: 14px;
	justify-content:flex-start;
	align-items:center;
	.text{
		color: #151924;
		line-height:1.4;
		width:70%;
	}
	.i-icon{
		width: 35px;
		margin: 0 17px 0 0;
		height: 35px;
	}
	.close-btn{
		cursor: pointer;
		position: absolute;
		padding-left:20px;
		font-weight: 600;
		right: 45px;
		height: 28px;
		letter-spacing: -0.25px;
		line-height:2;
		transition: opacity .3s ease-in-out;
		top:0;
		bottom:0;
		margin: auto;
		color: #434f6a;
		&:after{
			content: "";
			transition: opacity .3s ease-in-out;
			left: 0;
			top:0;
			bottom:0;
			margin: auto;
			background: url(../images/close-button.svg) no-repeat;
			width: 10px;
			height: 10px;
			position: absolute;
		}
		&:hover{
			opacity: 0.7;
			&:after{
				opacity: 0.7;
			}
		}
	}
	&.show{
		max-height: 100%;
		padding:28px 33px;
		opacity: 1;
		margin-bottom: 30px;
		visibility: visible;
	}
}

